import React from 'react';
import { Canvas } from './Canvas';

export const Node: React.FC = () => {
    const addNode = () => {
        const ctx: any = document?.getElementById('myCanvas')?.getContext('2d');
        ctx.beginPath();
        ctx.arc(Math.random(), Math.random(), 50, 0, Math.PI * 2, true);
        ctx.lineWidth = 2;
        ctx.stroke();
        ctx.font = '30px Arial';
        ctx.fillText('5', 65, 85);
    };
    return (
        <>
            <Canvas>
                <button onClick={() => addNode()}> Add Node </button>
            </Canvas>
        </>
    );
};
